.bookmarks-widget {
    background: rgba(255, 255, 255, 0.9);
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    max-width: 100%; /* Tam genilik iin ayarland */
    margin: 0 auto;
    font-family: Arial, sans-serif;
    overflow-x: auto; /* Yatay kaydrma iin ayarland */
  }
  
  .bookmarks-widget h2 {
    font-size: 1.5em;
    margin-bottom: 15px;
    color: #333;
    text-align: center;
  }
  
  .bookmarks-list {
    display: flex;
    flex-wrap: nowrap; /* Yatay dizilim iin ayarland */
    gap: 20px; /* Kartlar arasndaki boluk */
    list-style: none;
    padding: 0;
    margin: 0;
    overflow-x: auto; /* Yatay kaydrma iin ayarland */
  }
  
  .bookmark-item {
    flex: 0 0 auto; /* Kartlarn sabit genilii iin ayarland */
    width: 200px; /* Her bir kartn genilii */
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .bookmark-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .bookmark-link {
    display: flex;
    align-items: center;
    padding: 10px;
     
    color: white;
    text-decoration: none;
    border-radius: 8px;
    font-weight: 500;
    transition: background 0.3s ease, color 0.3s ease;
  }
  
  .bookmark-link:hover {
     
    color: #e0e0e0;
  }
  
  .bookmark-logo {
    width: 24px;
    height: 24px;
    margin-right: 10px;
    border-radius: 4px;
  }`, "",{"version":3,"sources":["webpack://./newtab/components/BookmarksWidget.css"],"names":[],"mappings":"AAAA,mCAAmC;;AAEnC;IACI,oCAAoC;IACpC,aAAa;IACb,mBAAmB;IACnB,yCAAyC;IACzC,eAAe,EAAE,gCAAgC;IACjD,cAAc;IACd,8BAA8B;IAC9B,gBAAgB,EAAE,kCAAkC;EACtD;;EAEA;IACE,gBAAgB;IAChB,mBAAmB;IACnB,WAAW;IACX,kBAAkB;EACpB;;EAEA;IACE,aAAa;IACb,iBAAiB,EAAE,iCAAiC;IACpD,SAAS,EAAE,8BAA8B;IACzC,gBAAgB;IAChB,UAAU;IACV,SAAS;IACT,gBAAgB,EAAE,kCAAkC;EACtD;;EAEA;IACE,cAAc,EAAE,6CAA6C;IAC7D,YAAY,EAAE,6BAA6B;IAC3C,qDAAqD;EACvD;;EAEA;IACE,2BAA2B;IAC3B,wCAAwC;EAC1C;;EAEA;IACE,aAAa;IACb,mBAAmB;IACnB,aAAa;;IAEb,YAAY;IACZ,qBAAqB;IACrB,kBAAkB;IAClB,gBAAgB;IAChB,iDAAiD;EACnD;;EAEA;;IAEE,cAAc;EAChB;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;EACpB","sourcesContent":["/* components/BookmarksWidget.css */\n\n.bookmarks-widget {\n    background: rgba(255, 255, 255, 0.9);\n    padding: 20px;\n    border-radius: 12px;\n    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);\n    max-width: 100%; /* Tam genilik iin ayarland */\n    margin: 0 auto;\n    font-family: Arial, sans-serif;\n    overflow-x: auto; /* Yatay kaydrma iin ayarland */\n  }\n  \n  .bookmarks-widget h2 {\n    font-size: 1.5em;\n    margin-bottom: 15px;\n    color: #333;\n    text-align: center;\n  }\n  \n  .bookmarks-list {\n    display: flex;\n    flex-wrap: nowrap; /* Yatay dizilim iin ayarland */\n    gap: 20px; /* Kartlar arasndaki boluk */\n    list-style: none;\n    padding: 0;\n    margin: 0;\n    overflow-x: auto; /* Yatay kaydrma iin ayarland */\n  }\n  \n  .bookmark-item {\n    flex: 0 0 auto; /* Kartlarn sabit genilii iin ayarland */\n    width: 200px; /* Her bir kartn genilii */\n    transition: transform 0.3s ease, box-shadow 0.3s ease;\n  }\n  \n  .bookmark-item:hover {\n    transform: translateY(-5px);\n    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  }\n  \n  .bookmark-link {\n    display: flex;\n    align-items: center;\n    padding: 10px;\n     \n    color: white;\n    text-decoration: none;\n    border-radius: 8px;\n